<template>
  <div class="home">
    <form action="">
      <h1>Buscador</h1>
      <label for="pokemon">Ingrese id Pokemon (1-200)</label> <br>
      <input type="number" name="pokemon" v-model="pokemon_id">
      <button v-on:click.prevent="search">Buscar</button>
    </form>

  </div>
</template>

<script>

export default {
  name: 'HomeView',
  components: {
  },
  data: function() {
    return {
      pokemon_id: 0
    }
  },
  methods: {
    search: function() {
      //evaluamos si la variable esta vacia para retornar el control al formulario
      //es decir, finaliza el trabajo del metodo search
      if(this.pokemon_id === "")
        return
      this.$router.push(`/pokemon/${this.pokemon_id}`)
    }
  }
}
</script>

<style scoped>
  form { 
    border:2px solid #42b983; 
    padding-bottom: 20px; 
    width:60%;
    margin:0 auto; 
    }
</style>
