<template>
    <div id="PokemonView">
        <h1>Componente PokemonView</h1>
        <h2>Pokemon: {{ name }}</h2>
        <img :src="image" alt="">
        <p><router-link to="/">Volver</router-link></p>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'PokemonView',
    props: ['id'], //los componentes pueden recibir informacion de la prop a traves de la ruta
    data: function() {
        return {
            name: '',
            image: '',
        }
    },
    methods: {
        fetchPokemon: function() {
            axios.get (`https://pokeapi.co/api/v2/pokemon/${this.id}`)
            .then (resp => {
                console.log(resp);
                this.setData(resp.data);
            })
            .catch(error => {
                console.log(error)
            })
        },
        setData(data){
            this.name = data.name;
            this.image = data.sprites.front_default;
        }
    },
    //Hook funcion que se llama cuando se cumplen ciertas condiciones
    created() {
        this.fetchPokemon();
    }
}
</script>

<style scoped>
#PokemonView {
    margin: 0 auto;
    border: 1px solid rebeccapurple;
    width: 50%;
}
img {
    width: 25%;
}
</style>