import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import PokemonView from '../views/PokemonView.vue'
import NotFound from '@/components/NotFound.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/pokemon/:id',
    name: 'pokemon',
    component: PokemonView,
    props: true //no hay un padre que envie el prop asi que se hace por el archivo de rutas, el prop serÃ¡ el mismo parametro del path
  },
  {
    path: '/:catchAll(.*)', //en vuejs 2 bastaba con el asterisco //alternativa: '/:pathMatch(.*)*'
    name: 'notFound',       //esta ruta siempre debe ir al final, para que compruebe primero las demÃ¡s
    component: NotFound
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
